import { useEffect, useState } from "react"
import { CloneElement } from "@rp/react/web/common/composite-components"
import { useLocalStorage } from "@rp/react/web/common/hooks"
import { Backdrop, Typography } from "@rp/react/web/mui/core"
import { WatchlistTutorialTooltip } from "../WatchlistDataTabs"
import { WATCHLIST_SMART_GRID_ADD_COLUMN_ID } from "./WatchlistSmartGridActions"

const WATCHLIST_SMART_GRID_TUTORIAL_VISITED_STORAGE_KEY = "watchlists.visitedSmartGridTutorial"

type Props = {
  onAddColumn: () => void
}

function WatchlistSmartGridTutorial({ onAddColumn }: Props) {
  const [visitedSmartGridTutorial, setVisitedSmartGridTutorial] = useLocalStorage<boolean>(
    WATCHLIST_SMART_GRID_TUTORIAL_VISITED_STORAGE_KEY,
    false,
  )
  const [open, setOpen] = useState<boolean>(false)

  useEffect(() => {
    setTimeout(() => setOpen(!visitedSmartGridTutorial), 300)
  }, [visitedSmartGridTutorial])

  return (
    <Backdrop
      sx={{
        color: "#fff",
        zIndex: theme => theme.zIndex.drawer + 1,
        "& a, & button": { cursor: "default" },
      }}
      open={!visitedSmartGridTutorial}
    >
      <WatchlistTutorialTooltip
        offsetX={-130}
        onDone={() => {
          setVisitedSmartGridTutorial(true)
          setOpen(false)
          onAddColumn()
        }}
        onClose={() => {
          setVisitedSmartGridTutorial(true)
          setOpen(false)
        }}
        title="Welcome to SmartGrid"
        description={
          <Typography sx={{ fontSize: 14, color: "text.secondary" }}>
            Click the + button to add a new column. Enter your prompt to receive insights for each
            entity in your watchlist.
          </Typography>
        }
        open={open}
      >
        <CloneElement
          selector={`#${WATCHLIST_SMART_GRID_ADD_COLUMN_ID}`}
          onClick={() => {
            setVisitedSmartGridTutorial(true)
            setOpen(false)
            onAddColumn()
          }}
        />
      </WatchlistTutorialTooltip>
    </Backdrop>
  )
}

export { WatchlistSmartGridTutorial, WATCHLIST_SMART_GRID_TUTORIAL_VISITED_STORAGE_KEY }
